.eb-toc-container {
  margin: 0 auto;
  width: 100%; }

.eb-toc-container > * {
  position: relative; }

.eb-toc-wrapper {
  width: 100%; }

.eb-toc__list-wrap ul {
  display: inline-block;
  text-align: left;
  margin: 0em 0em 1.5em 1.3em;
  padding: 0em 0em 0em 1.3em; }

.eb-toc__list-wrap ol {
  counter-reset: item;
  margin: 0em 0em 1.5em 1.3em;
  padding: 0em 0em 0em 1.3em; }
  .eb-toc__list-wrap ol li {
    display: block; }
  .eb-toc__list-wrap ol li:before {
    content: counters(item, ".") " ";
    counter-increment: item; }

.eb-toc__list ul {
  margin-bottom: 0; }

.eb-typography-base > div {
  display: flex;
  justify-content: space-between; }

.hide-content {
  display: none; }

.eb-toc-go-top {
  height: 30px;
  width: 30px;
  position: fixed;
  bottom: 50px;
  right: 50px;
  background: #ccd0d4;
  justify-content: center;
  align-items: center;
  cursor: pointer; }

.show-scroll {
  display: flex; }

.hide-scroll {
  display: none; }

.eb-toc-header {
  position: relative; }

.eb-toc-button {
  position: relative;
  display: inline-block;
  font-size: 80% !important;
  font-weight: 400;
  text-align: center;
  text-decoration: none;
  text-transform: none;
  color: #fff;
  padding: 10px 20px;
  border-radius: 3px;
  border: none;
  transform: rotate(90deg);
  transform-origin: bottom left;
  cursor: pointer; }

.eb-toc-close {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: -9px;
  top: -11px;
  width: 28px;
  height: 28px;
  background: #fff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 20px;
  font-family: serif;
  font-size: 20px;
  font-weight: 700;
  line-height: 28px;
  text-align: center;
  cursor: pointer;
  padding: 0;
  margin: 0;
  border: 0;
  opacity: 0.8; }
  .eb-toc-close:hover {
    opacity: 1; }
  .eb-toc-close:before, .eb-toc-close:after {
    position: absolute;
    content: " ";
    height: 12px;
    width: 2px;
    background-color: #333; }
  .eb-toc-close:before {
    transform: rotate(45deg); }
  .eb-toc-close:after {
    transform: rotate(-45deg); }
